	},
	computed: {
		showLeftDrawer: {
			get() {
				const showLeftDrawer = this.PCIInfo.list.length != 0 || Object.keys(this.facilityInfo.info).length != 0 || Object.keys(this.sidewalkInfo.info).length != 0;
					
				if(showLeftDrawer) {
					// PCI選單偏移
					if(this.$refs.PCILevelMenu) {
						const left_PCILevelMenu = this.$refs.PCILevelMenu.getBoundingClientRect().left;
						if(left_PCILevelMenu < 400) {
							this.cssTemp.PCILevelMenu = left_PCILevelMenu;
							this.$refs.PCILevelMenu.style.left = `${left_PCILevelMenu + 400}px`;
						}
					}

					// 圖層按鈕偏移
					const btnDistrictList = Array.from(this.$el.querySelectorAll(".btn_district"));
					let left_btnDistrict_list = [];
					for(const btnDistrict of btnDistrictList) left_btnDistrict_list.push(btnDistrict.getBoundingClientRect().left);
					if( left_btnDistrict_list.filter(left => left >= 400).length == 0) {
						this.cssTemp.btnDistrict = left_btnDistrict_list;
						for(const index in btnDistrictList) btnDistrictList[index].style.left = `${left_btnDistrict_list[index] + 400}px`;
					}
				}
				return showLeftDrawer;
			},
			set(value) {
				if(!value) {
					// console.log(value);
					this.PCIInfo.list = [];
					Object.keys(this.facilityInfo.info).forEach(key => delete this.facilityInfo.info[key]);
					Object.keys(this.sidewalkInfo.info).forEach(key => delete this.sidewalkInfo.info[key]);

					// PCI選單歸位
					if(this.$refs.PCILevelMenu && this.cssTemp.PCILevelMenu) this.$refs.PCILevelMenu.style.left = `${this.cssTemp.PCILevelMenu}px`;
					
					// 圖層按鈕歸位
					const btnDistrictList = Array.from(this.$el.querySelectorAll(".btn_district"));
					let left_btnDistrict_list = [];
					for(const btnDistrict of btnDistrictList) left_btnDistrict_list.push(btnDistrict.getBoundingClientRect().left);
					if(this.cssTemp.btnDistrict && this.cssTemp.btnDistrict.length != 0) {
						for(const index in btnDistrictList) btnDistrictList[index].style.left = `${this.cssTemp.btnDistrict[index]}px`;
					}
				}
				return this.PCIInfo.list.length || Object.keys(this.facilityInfo.info).length || Object.keys(this.sidewalkInfo.info).length != 0;
			}
		},
		dateStr() {
			return this.listQuery.year + String(this.listQuery.month).padStart(2, "0");
		},
		streetViewList() {
			// NOTE: 區域改變時，也要讓streetViewList產生變動
			if(this.listQuery.layerType || this.listQuery.distIndex || this.listQuery.year || this.listQuery.month || this.activeLayer.panoramaLayer) ;

			let streetViewList = {};

			for(const type of this.listQuery.streetViewType) {
				if (!this.panoramaInfo.list.hasOwnProperty(this.listQuery.distIndex) || !this.panoramaInfo.list[this.listQuery.distIndex].hasOwnProperty(type)) continue;
				const lineInfoList = this.panoramaInfo.list[this.listQuery.distIndex][type];
				const panoramaUrl = this.layerList.panorama.assetsUrl;

				for (const lineInfo of lineInfoList) {
					lineInfo.forEach((info, index) => {
						const panoramaPhotoUrl = (this.config.name == "demo") ? 
							`${panoramaUrl}/${type}/${this.listQuery.distIndex}/${this.dateStr}/${info.fileName}.jpg` :
							`${panoramaUrl}/${this.listQuery.distIndex}/${info.fileName}.jpg`;

						// NOTE: 測試調整角度
						const offsetYaw = 0;

						streetViewList[info.fileName] = {
							type: "equirectangular",
							panorama: panoramaPhotoUrl,
							hfov: this.panoramaInfo.setting[type].hfov,
							yaw: this.panoramaInfo.setting[type].yaw + offsetYaw,
							horizonRoll: this.panoramaInfo.setting[type].horizonRoll,
							compass: true,
							northOffset: info.azimuth,
							autoLoad: true,
						};

						let hotSpots = [];
						if (index >= 0 && index < lineInfo.length - 1) {
							hotSpots.push({
								pitch: this.panoramaInfo.setting[type].pitch,
								yaw: this.panoramaInfo.setting[type].yaw + offsetYaw,
								type: "scene",
								text: lineInfo[index + 1].fileName,
								sceneId: lineInfo[index + 1].fileName,
							});
						}

						if (index > 0 && index < lineInfo.length) {
							hotSpots.push({
								pitch: this.panoramaInfo.setting[type].pitch,
								yaw: this.panoramaInfo.setting[type].yaw + 180 + offsetYaw,
								type: "scene",
								text: lineInfo[index - 1].fileName,
								sceneId: lineInfo[index - 1].fileName,
								targetYaw: this.panoramaInfo.setting[type].yaw + 180 + offsetYaw,
							});
						}

						if (this.panoramaInfo.hotSpotsList.hasOwnProperty(this.listQuery.distIndex) && this.panoramaInfo.hotSpotsList[this.listQuery.distIndex].hasOwnProperty(type)) {
							const hotSpotList = this.panoramaInfo.hotSpotsList[this.listQuery.distIndex][type];
							if(hotSpotList[info.fileName] != undefined) {
								for(const hotSpot of hotSpotList[info.fileName]) {
									hotSpots.push(hotSpot);
								}
							}
						}

						// hotSpots.push({
						// 	pitch: -15,
						// 	yaw: 43,
						// 	type: "info",
						// 	cssClass: "hotSpotIcon",
						// 	text: `我是矮不隆咚的變電箱
						// 		哈哈哈
						// 		惶恐灘頭說惶恐，零丁洋裏嘆零丁。
						// 		人生自古誰無死？留取丹心照汗青。`,
						// 	URL: "https://zh.wikipedia.org/zh-tw/%E9%85%8D%E9%9B%BB%E7%9B%A4"
						// })

						streetViewList[info.fileName]["hotSpots"] = hotSpots;
					});
				}	
			}

			return streetViewList;
		},
		PCIKMLUrl() {
			// kml網址: ${kmlUrl}/kml/PCI/${年月字串}/3_1_${區域號}_${流水號}P${PCI等級}_${版本號}.KML

			const kmlUrl = this.layerList.PCI.assetsUrl;
			let urlObj = {};
			for (const level of [...Object.keys(this.PCILevel), ...Object.keys(this.areaList) ]) {
				// NOTE: KML需統一格式
				if(this.config.name == "demo") urlObj[level] = `${kmlUrl}/${this.dateStr}/3_1_${this.listQuery.distIndex}_P${level}_${this.kmlFileLastNum[this.dateStr]}.KML`;
				// if(this.config.name == "demo") urlObj[level] = `https://storage.googleapis.com/map_kml/3_1_0_P4_4.KML`;
				// urlObj[level] = `${kmlUrl}/kml/PCI/200002/3_1_${this.listQuery.distIndex}_P${level}_12.KML`;
				// urlObj[level] = `${kmlUrl}/kml/PCI/202205/3_1_${this.listQuery.distIndex}_P${level}_23.KML`;
				else if(this.config.name == "freeway") urlObj[level] = `${kmlUrl}/${this.dateStr}/PCI_${level}_${this.kmlFileLastNum[this.dateStr]}.KML`;
			}

			return urlObj;
		},
		caseKMLUrl() {
			// kml網址: ${kmlUrl}/kml/PCI/${年月字串}/3_1_${區域號}_${流水號}pointP${PCI等級}_${版本號}.KML

			const kmlUrl = this.layerList.PCI.assetsUrl;
			let urlObj = {};
			for (const level of [...Object.keys(this.PCILevel), ...Object.keys(this.areaList) ]) {
				// NOTE: 測試GeoJson -> Load Local KML
				// if(this.config.name == "demo") urlObj[level] = `${this.publicPath}assets/kml/PCI/${this.dateStr}/3_1_${this.listQuery.distIndex}_1pointP_${level}_${kmlFileLastNum[this.dateStr]}.KML`;
				// NOTE: KML需統一格式
				if(this.config.name == "demo") urlObj[level] = `${kmlUrl}/${this.dateStr}/3_1_${this.listQuery.distIndex}_1pointP_${level}_${this.kmlFileLastNum[this.dateStr]}.KML`;
				else if(this.config.name == "freeway") urlObj[-1] = `${kmlUrl}/${this.dateStr}/PCI_Case_${this.kmlFileLastNum[this.dateStr]}.KML`;
			}

			return urlObj;
		},
		districtListFilter() {
			return Object.fromEntries(Object.entries(this.districtList).filter(([key, _]) => key != this.listQuery.distIndex));
		},
		caseTypeListFilter() {
			let listFilter = Object.fromEntries(Object.entries(this.caseTypeList).filter(([key, _]) => key != "etc"));
			if(this.listQuery.caseEtc) Object.assign(listFilter, this.caseTypeList.etc.list);
			if(this.listQuery.caseType != -1) listFilter = Object.fromEntries(Object.entries(listFilter).filter(([key, _]) => key == this.listQuery.caseType));

			return listFilter;
		},
		markersFilter() {
			// NOTE: 區域改變時，也要讓markersFilter產生變動
			if(this.listQuery.distIndex || this.activeLayer.case) ;

			let markersCase = Object.fromEntries(Object.entries(this.markers.case).filter(([key, _]) => this.listQuery.PCILevel.includes(Number(key))));
			if(this.listQuery.PCILevel.length > 0 && Object.keys(this.caseKMLUrl).length == 1 && this.caseKMLUrl.hasOwnProperty("-1")) markersCase = this.markers.case;
			if(this.listQuery.caseType == -1 && this.listQuery.caseLevel == -1 && this.listQuery.caseTypeSpec == -1) return Object.values(markersCase).flat();

			const caseTypeInfo = this.caseTypeListFilter[this.listQuery.caseType];
			let filterStrList = [];
			if(this.listQuery.caseTypeSpec == -1) {
				if(caseTypeInfo.filter != undefined) filterStrList = caseTypeInfo.filter; 
				else filterStrList.push(caseTypeInfo.name);
			} else filterStrList.push(caseTypeInfo.list[this.listQuery.caseTypeSpec]);

			if(this.listQuery.caseLevel != -1) filterStrList = filterStrList.map(str => str += `（${this.caseLevel[this.listQuery.caseLevel].description}）`);
			console.log(filterStrList);
			
			return Object.values(markersCase).flat().filter(marker => {
				for(const filterStr of filterStrList) return marker.description.indexOf(filterStr) != -1
			});
		},
		PCIInfoTotal() {
			const total = this.PCIInfo.list.length | 0;
			const areaTotal = this.PCIInfo.list.reduce((curr, prev) => (curr += Number(prev.area)), 0);
			const scoreAverage = this.PCIInfo.list.reduce((curr, prev) => {
				if(Number(prev.score)) curr += Number(prev.score);
				else curr += Number(prev.score.split("→")[1]);
				return curr;
			}, 0) / total;

			return { total, areaTotal, scoreAverage: scoreAverage.toFixed(2) };
		},
	},
	created() {
		// 清除 session.devMode
		const now = new Date().getTime();
		if(now > sessionStorage.dueTime) sessionStorage.clear();

		const url = new URL(window.location.href);
		this.config.name = url.searchParams.get("c") || "demo";
		this.config.area = url.searchParams.get("area");
		this.config.latLng = { lat: Number(url.searchParams.get("lat")), lng: Number(url.searchParams.get("lng")) };
		this.config.zoom = url.searchParams.get("zoom");
		this.listQuery.streetViewPointMode = url.searchParams.has("p") || false;
		this.config.hotSpotsDebugMode = url.searchParams.has("h") || false;
		// console.log(this.config);

		//讀取設定檔
		const configUrl = this.config.localEnv ? `${this.publicPath}assets/config/${this.config.name}/${this.config.name}_local.json` : `https://storage.googleapis.com/map_config/${this.config.name}/${this.config.name}.json?t=${Date.now()}`;
		// const configUrl = `https://storage.googleapis.com/map_config/${this.config.name}/${this.config.name}.json?t=${Date.now()}`;
		fetch(configUrl).then(async (response) => {
			const { layerList, districtList } = await response.json();
			
			// 載入區域資料
			this.districtList = districtList;
			this.listQuery.distIndex = this.config.area || Object.keys(this.districtList)[0];

			// 載入layer資料
			this.layerList = layerList;
			for(const layerName in this.layerList) {
				if(!this.kmlLayer.hasOwnProperty(`${layerName}Layer`)) this.$set(this.kmlLayer, `${layerName}Layer`, null);
				if(!this.activeLayer.hasOwnProperty(`${layerName}Layer`)) this.$set(this.activeLayer, `${layerName}Layer`, false);

				//NOTE: KML在本地端
				this.layerList[layerName].assetsUrl = this.layerList[layerName].assetsUrl.replace("${WebUrl}", WebUrl);
			}

			// 載入KML資訊
			if(this.layerList.PCI.options.kmlFileLastNum) this.kmlFileLastNum = this.layerList.PCI.options.kmlFileLastNum;

			// 載入case資料
			this.caseTypeList = this.layerList.PCI.options.caseTypeList;

			// 載入PCIInfo資料
			this.PCIInfo.headers = this.layerList.PCI.options.PCIInfo.headers.filter(h => !["order", "id"].includes(h.value));
			this.PCIInfo.regex = this.layerList.PCI.options.PCIInfo.regex;
			this.layerList.PCI.options.PCIInfo.headers.forEach((header, index) => this.PCIInfo.infoOrder[header.value] = index);

			// 載入設施物資料
			if(this.layerList.facility) {
				this.facilityInfo.type = this.layerList.facility.options.facilityType;
				this.facilityInfo.geoJSONUrl = this.layerList.facility.options.geoJSONUrl;
				this.facilityInfo.headers = this.layerList.facility.options.facilityInfo.headers;
				//NOTE: 預設設施類型全選
				this.listQuery.facilityType = Object.keys(this.facilityInfo.type);
			}

			// 載入人行道資料
			if(this.layerList.sidewalk) {
				this.sidewalkInfo.type = this.layerList.sidewalk.options.sidewalkType;
				this.sidewalkInfo.geoJSONUrl = this.layerList.sidewalk.options.geoJSONUrl;
				this.sidewalkInfo.headers = this.layerList.sidewalk.options.sidewalkInfo.headers;
			}

			// 載入全景資料
			this.panoramaInfo.type = layerList.panorama.options.streetViewType;
			this.panoramaInfo.streetViewsJSONUrl = layerList.panorama.options.streetViewJSON;
			this.panoramaInfo.hotSpotsJSONUrl = layerList.panorama.options.hotSpotsJSON;
			this.panoramaInfo.monthRestriction.panorama = layerList.panorama.options.monthRestriction;
			this.setHistHighlight();

			//NOTE: 預設街景類型全選
			this.listQuery.streetViewType = Object.keys(this.panoramaInfo.type);
			
			// Google Map錯誤處理
			window.gm_authFailure = () => { 
				console.log("Google Map Failure");
				// session.devMode: 時效5分鐘
				sessionStorage.devMode = true;
				sessionStorage.dueTime = new Date().getTime() + (5*60*1000);
				location.reload();
			};

			loader.load().then(() => this.initMap()).catch(err => console.log("err: ", err));

		}).catch((err) => {
			console.log(err);
			// url.searchParams.set("config", "demo");
			window.location.href = url.origin + url.pathname;
		});
	},
