	methods: {
		// init google map
		initMap() {
			// 預設顯示的地點：台北市政府親子劇場
			// const location = {
			//   lat: 25.0374865, // 經度
			//   lng: 121.5647688, // 緯度
			// };

			const location = (this.config.latLng.lat && this.config.latLng.lng ) ? this.config.latLng : this.districtList[this.listQuery.distIndex].position;

			// 建立地圖
			this.map = new google.maps.Map(this.$refs.map, {
				center: location, // 中心點座標
				zoom: Number(this.config.zoom) || 14, // 1-20，數字愈大，地圖愈細：1是世界地圖，20就會到街道
				maxZoom: 20,
				minZoom: 13,
				/*
					roadmap 顯示默認道路地圖視圖。
					satellite 顯示 Google 地球衛星圖像。
					hybrid 顯示正常和衛星視圖的混合。
					terrain 顯示基於地形信息的物理地圖。
				*/
				// mapTypeId: "roadmap",
				fullscreenControl: false,
				mapTypeControl: false,
				streetViewControl: false,
				rotateControl: false,
				tilt: 0,
				styles: [
					{
						stylers: [{ visibility: "on" }, { "lightness": 10 },
							// { "invert_lightness": true }
						],
					},
					{
						featureType: "poi",
						elementType: "all",
						stylers: [{ visibility: "off" }],
					},
					{
						featureType: "transit",
						elementType: "all",
						stylers: [{ visibility: "off" }],
					},
					{
						featureType: "road",
						elementType: "labels",
						stylers: [{ visibility: "off" }]
					},
					{
						featureType: "road",
						elementType: "geometry",
						stylers: [{ "saturation": -40 }, { "lightness": -5 }]
					},
					{
						featureType: "landscape",
						elementType: "geometry",
						stylers: [{ "saturation": -50 }, { "lightness": -5 }]
					},
					// {
					// 	featureType: "road",
					// 	elementType: "geometry",
					// 	stylers: [{ "saturation": 5 }, { "lightness": -40 }, { "invert_lightness": true }
					// 	],
