					// {
					// 	featureType: "landscape",
					// 	elementType: "geometry",
					// 	stylers: [{ "saturation": -80 }, { "lightness": -30 }]
					// }
				],
			});

			// NOTE: 設定路名在KML之上，只有在非開發模式才能載入多圖層
			if(loaderOpt.apiKey.length != 0) {
				// NOTE: 疊上StyledMapType
				const labelsMapType = new google.maps.StyledMapType(
					[
						{
							stylers: [{ visibility: 'off'}]
						}, 
						{
							featureType: "road",
							elementType: 'labels',
							stylers: [{ visibility: 'on' }]
						}
					], 
					{
						name: 'Labels'
					}
				);
				this.map.overlayMapTypes.push(labelsMapType);
			}

			// 掛載place server
			this.placeServer = new google.maps.places.PlacesService(this.map);

			// 掛載 PCI KmlLayer
			for (const level of [...Object.keys(this.PCILevel), ...Object.keys(this.areaList) ]) {
				this.kmlLayer.PCILayer[level] = new google.maps.KmlLayer({
					url: this.PCIKMLUrl[level],
					map: this.map,
					suppressInfoWindows: true,
					preserveViewport: true,
					// zIndex: Number(level)
				});
				// this.kmlLayer.PCILayer.setMap(this.map);

				this.kmlLayer.PCILayer[level].addListener("click", (event) => {
					const description = event.featureData.name;
					// console.log(description);

					let regexStr = level >= 7 ? this.PCIInfo.regex.changed : this.PCIInfo.regex.default;
					const regex = new RegExp(regexStr, "s");
					// console.log(regex);

					//ID：5971，區塊編號：0101281000030，地點：龍江路429巷，面積：172㎡，道路編號：003-路口，PCI統計日期：2022/05/01~2022/05/25，PCI程度：很好，PCI分數：100
					const content = description.split(regex).filter(str => str.length != 0);
						// .split(/ID：(\d*)，區塊編號：(\d+)，地點：(.*)，面積：(\d*)㎡，道路編號：(.*)，PCI統計日期：.*?，PCI分數：(\d*)/s)
					// console.log(content);

					const isSame = this.PCIInfo.list.some(l => l.order == content[0]);
					if (!isSame) {
						let item = {};
						for(const header of Object.keys(this.PCIInfo.infoOrder)) {
							const infoOrder = this.PCIInfo.infoOrder[header];
							if(level < 7 || header != "score") item[header] = content[infoOrder];
							else item[header] = `${content[infoOrder]} → ${content[infoOrder+1]}`;
						}
						this.PCIInfo.list.push(item);
					}
				});
			}

			// 掛載 Case KmlLayer
