			// 監聽data layer
			// this.map.data.addListener('click', (event) => {
			// 	this.infoWindow.setContent(event.feature.h.description);
			// 	this.infoWindow.setPosition(event.latLng);
			// 	this.infoWindow.open(this.map);
			// });

			// 建立 人行道 data layer
			this.dataLayer.sidewalk = {};
