					// 	this.infoWindow.setContent(`ogc_fid: ${event.feature.h.ogc_fid}`);
					// 	this.infoWindow.setOptions({ pixelOffset: new google.maps.Size(0, -10)});
					// 	this.infoWindow.setPosition(event.latLng);

					// 	this.infoWindow.open(this.map);
					// });

					// this.dataLayer.sidewalk.block.addListener("mouseout", (event) => {
					// 	this.infoWindow.close();
					// });
				} 
				
				if(type == "case") {
					// 案件
					this.dataLayer.sidewalk.case.setStyle({
						icon: { 
							url: `${this.publicPath}assets/icon/icon_redDot.png`, 
							scaledSize: new google.maps.Size(5, 5) 
						} 
					});

					this.dataLayer.sidewalk.case.addListener('click', (event) => {
						this.markerPos = event.latLng.toJSON();
						// console.log(event);
						const description = 
							`<table border=0 width="300">
								<tr>
									<td align="left" colspan="2">
										<font size=4>缺失名稱：${event.feature.h.caseName}</font>
									</td>
								</tr>
								<tr><td align="left" colspan="2"></td></tr>
								</table>
								<button type="button" id="info-panorama-btn" class="v-btn v-btn--plain theme--light v-size--x-small" style="height: 40px; width: 40px;">
									<span class="v-btn__content">
										<i aria-hidden="true" class="v-icon notranslate mdi mdi-eye-arrow-left theme--dark" style="font-size: 30px; color: #FFF; caret-color: #2196F3;"></i>
									</span>
								</button>`;
						this.infoWindow.setContent(description);
						this.infoWindow.setPosition(event.latLng);
						this.infoWindow.open(this.map);
					});
				}
			}

			// NOTE: 載入其他KML圖層
			for(const layerName in this.kmlLayer) {
				if(layerName == "PCILayer" || layerName == "caseLayer") continue;
				this.kmlLayer[layerName] = new google.maps.KmlLayer({
					map: this.map,
					// suppressInfoWindows: true,
					preserveViewport: true,
				});
			}

			// NOTE: 載入全景Marker測試資料
			for (let markerName in this.markerList_test) {
				this.markers[markerName] = new google.maps.Marker({
					title: markerName,
					position: this.markerList_test[markerName],
				});

				this.markers[markerName].addListener("click", (event) => {
					const sceneId = this.markers[markerName].getTitle();
					console.log(sceneId);
					this.showPanoramaLayer(sceneId);
				});
			}

			// NOTE: 測試正射圖
			// const imageBounds = {
			// 	north: 25.0517653555303,
			// 	south: 25.051629859376,
			// 	east: 121.532938407293,
			// 	west: 121.532845725004,
			// };

			// new google.maps.GroundOverlay(
			// 	"https://storage.googleapis.com/demo-freeway20200701/Orthos-0-1.png",
			// 	imageBounds,
