		setCenter(location) {
			if(location == undefined) location = this.districtList[this.listQuery.distIndex].position;
			this.map.setCenter(location);
			
			// console.log(this.map.getZoom());
			this.map.setZoom(13);
		},
		setMapType(type) {
			this.map.setMapTypeId(type);
			this.listQuery.mapType = type;
		},
		search() {
			// if (this.listQuery.searchText.length == 0) this.listQuery.searchText = this.districtList[0].name;

			if(loaderOpt.apiKey.length == 0) {
				for (let index in this.districtList) {
					if (this.listQuery.distIndex != index && this.districtList[index].name.indexOf(this.listQuery.searchText) != -1) this.setDistIndex(index);
				}
			} else {
				// NOTE: Place Server 需要 google console 設定
				const request = {
					fields: ['geometry'],
					query: this.listQuery.searchText
				};
				this.placeServer.findPlaceFromQuery(request, (result, status) => {
					// console.log(result, status);
					if(status === google.maps.places.PlacesServiceStatus.OK && result)
						this.map.setCenter(result[0].geometry.location);
						this.map.fitBounds(result[0].geometry.viewport);
				});
			}
		},
		setHistHighlight() {
			this.$el.querySelectorAll(".month-block .v-slider__tick-label").forEach(el => el.classList.remove("highlight"));
			if(Object.keys(this.panoramaInfo.monthRestriction).length == 0) return;
			if(this.panoramaInfo.monthRestriction[this.listQuery.layerType] == undefined) return;
			let monthRestrictionSpec = this.panoramaInfo.monthRestriction[this.listQuery.layerType];
			if(monthRestrictionSpec[this.listQuery.distIndex] == undefined || monthRestrictionSpec[this.listQuery.distIndex][this.listQuery.year] == undefined) return;
			monthRestrictionSpec = monthRestrictionSpec[this.listQuery.distIndex][this.listQuery.year];

			this.$el.querySelectorAll(".month-block .v-slider__tick-label").forEach((el, i) => {
				if(monthRestrictionSpec.includes(i+1)) el.classList.add("highlight");
			});

			// TODO: 測試自動跳轉month
			this.listQuery.month = monthRestrictionSpec[monthRestrictionSpec.length - 1];
		},
		setLayer(layerType) {
			this.showLayerMenu = false;
			this.clearAllLayer();
			for(const layerName in this.activeLayer) this.activeLayer[layerName] = false;
			this.activeLayer[`${layerType}Layer`] = true;

			const type = this.layerList[layerType].type;
			switch (type) {
				case "PCI":
					this.setPCILayer();
					break;
				case "facility": 
					this.setFacilityLayer();
					break;
				case "sidewalk": 
					this.setSidewalkLayer();
					break;
				case "panorama":
					this.PCIInfo.list = [];
					this.setPanoramaLayer();
					break;
				case "default":
					this.PCIInfo.list = [];
					this.setDefaultLayer(layerType);
					break;
			}

			this.setHistHighlight();
		},
		setDistIndex(distIndex) {
			this.showDistrictMenu = false;
			this.clearAllLayer();

			if (distIndex != undefined) this.listQuery.distIndex = Number(distIndex);
			// this.listQuery.searchText = this.districtList[this.listQuery.distIndex].name;

			// 改變位置
			this.setCenter();

			this.setHistHighlight();
			this.setPCILayer();
			this.setCaseLayer(true);
			this.setPanoramaLayer();
			this.setFacilityLayer();
