		setPCILayer(level) {
			for (const [_, layer] of Object.entries(this.kmlLayer.PCILayer)) layer.setMap(null);
			if (!this.activeLayer.PCI) return;

			// level == -2，全部; level == -1，清除 ; level >= 0，先清除在讀入
			if (level == -2) this.listQuery.PCILevel = this.PCILevelOrder;
			if (level >= -1) this.listQuery.PCILevel = [];
			if (level > -1) this.listQuery.PCILevel = [level];

			//如果包含 變差區域(PCI LV7) 和 變好區域(PCI LV8)，應先剔除
			const PCILevelFilter = this.listQuery.PCILevel.filter(level => ![7, 8].includes(level));
			if (PCILevelFilter.length > 0) {
				this.listQuery.PCILevel = PCILevelFilter;
				// this.PCIInfo.list = [];
			}

			for (const level of this.listQuery.PCILevel) {
				console.log(this.PCIKMLUrl[level]);
				this.kmlLayer.PCILayer[level].setUrl(this.PCIKMLUrl[level]);
				if (this.kmlLayer.PCILayer[level].getMap() == null) this.kmlLayer.PCILayer[level].setMap(this.map);
			}

			this.listQuery.layerType = "PCI";
		},
		async kmlToGeoJson(level) {
			this.markers.case[level] = [];
			if(this.caseKMLUrl[level] == undefined) return new Promise((resolve, reject) => resolve());

			// NOTE: Load GeoJson
			await fetch(`${this.caseKMLUrl[level]}?t=${Date.now()}`)
			// await fetch(`${this.publicPath}assets/kml/3_1_1_2pointP_7_23.KML`)
			// fetch("https://storage.googleapis.com/demo-freeway20200701/3_1_1_2pointP_7_23.KML")
				.then(response => response.text()).then(xml => { 
					// this.map.data.addGeoJson(kml(new DOMParser().parseFromString(xml, "text/xml")));
					let geoJson = kml(new DOMParser().parseFromString(xml, "text/xml"));

					for(const markerInfo of geoJson.features) {
						const point = new google.maps.LatLng(markerInfo.geometry.coordinates[1], markerInfo.geometry.coordinates[0]);
						const marker = new google.maps.Marker({ 
							position: point, 
							description: markerInfo.properties.description.value,
							icon: { 
								url: markerInfo.properties.icon.replace("http://taipeinw.bim-group.com/", `${this.publicPath}assets/`), 
								scaledSize: new google.maps.Size(30, 30) 
							} 
						});
						this.markers.case[level].push(marker);

						marker.addListener('click', (event) => {
							console.log(marker.description);
							this.markerPos = event.latLng.toJSON();
							let description = marker.description.replace('border="1"', 'border="0"');
							// const DOM = new DOMParser().parseFromString(description, "text/html");
							// console.log(DOM.querySelector("html").querySelector("body").querySelector("table"));
							description += `<button type="button" id="info-panorama-btn" class="v-btn v-btn--plain theme--light v-size--x-small" style="height: 40px; width: 40px;"><span class="v-btn__content"><i aria-hidden="true" class="v-icon notranslate mdi mdi-eye-arrow-left theme--dark" style="font-size: 30px; color: #FFF; caret-color: #2196F3;"></i></span></button>`;
							this.infoWindow.setContent(description);
							this.infoWindow.setPosition(event.latLng);
							this.infoWindow.open(this.map);

							// this.infoWindow.addListener('content_changed', () => {
							// 	console.log('content_changed');
							// 	this.infoPanoramaBtn.removeEventListener('click', this.showSpecPanorama);
							// 	this.infoPanoramaBtn = null;
							// })
						});
					}

					return new Promise((resolve, reject) => resolve());
				});
		},
