		async setPanoramaLayer() {
			// for (const polyLineList of Object.values(this.polyLine)) for (const polyLine of Object.values(polyLineList)) polyLine.setMap(null);
			for(const polyline of Object.values(this.flattenObj(this.polyLine)).flat()) polyline.setMap(null);
			// for (const markers of Object.values(this.markers.panorama)) for(const marker of Object.values(markers)) marker.setMap(null);
			for(const markers of Object.values(this.flattenObj(this.markers.panorama)).flat()) markers.setMap(null);
			if (!this.activeLayer.panoramaLayer) return;

			for await (const res of this.listQuery.streetViewType.map(type => this.getStreetViewJSON(type)));

			if(this.listQuery.streetViewPointMode) for await (const res of this.listQuery.streetViewType.map(type => this.createMarker(type)));
			else for await (const res of this.listQuery.streetViewType.map(type => this.createPolyLine(type)));

			// for (const markers of Object.values(this.markers)) markers.setMap(this.map);
			this.listQuery.layerType = "panorama";
		},
		async getStreetViewJSON(type) {
			if(this.panoramaInfo.streetViewsJSONUrl == undefined) return new Promise((resolve, reject) => resolve());

			//NOTE: KML在本地端
			const panoramaUrl = this.panoramaInfo.streetViewsJSONUrl.replace("${publicPath}", this.publicPath).replace("${type}", type).replace("${dist}", this.listQuery.distIndex).replace("${dateStr}", this.dateStr);
			let hotSpotsUrl = "";
			if(this.panoramaInfo.hotSpotsJSONUrl != undefined) hotSpotsUrl = this.panoramaInfo.hotSpotsJSONUrl.replace("${publicPath}", this.publicPath).replace("${type}", type).replace("${dist}", this.listQuery.distIndex).replace("${dateStr}", this.dateStr);
			await fetch(`${panoramaUrl}?t=${Date.now()}`).then(async (response) => {
				if(!this.panoramaInfo.list.hasOwnProperty(this.listQuery.distIndex)) this.panoramaInfo.list[this.listQuery.distIndex] = {};
				if(!response.ok) {
					this.panoramaInfo.list[this.listQuery.distIndex][type] = [];
					this.panoramaInfo.setting[type] = {};
					return new Promise((resolve, reject) => resolve());
				}

				const json = await response.json();
				this.panoramaInfo.list[this.listQuery.distIndex][type] = json.data;
				this.panoramaInfo.setting[type] = json.sceneSetting;

				if(hotSpotsUrl.length != 0) {
					await fetch(`${hotSpotsUrl}?t=${Date.now()}`).then(async (response) => {
						if(!this.panoramaInfo.hotSpotsList.hasOwnProperty(this.listQuery.distIndex)) this.panoramaInfo.hotSpotsList[this.listQuery.distIndex] = {};
						if(!response.ok) {
							this.panoramaInfo.hotSpotsList[this.listQuery.distIndex][type] = [];
							return new Promise((resolve, reject) => resolve());
						} else {
							const json = await response.json();
							this.panoramaInfo.hotSpotsList[this.listQuery.distIndex][type] = json.data;
							return new Promise((resolve, reject) => resolve());
						}
					});
				} else return new Promise((resolve, reject) => resolve());
			});
		},
		setPanoramaScene() {
			// 掛載panorama scene
			for (const sceneId of this.prevSceneId) this.panorama.removeScene(sceneId);
			this.prevSceneId = [];
			for (const sceneId in this.streetViewList) {
				this.panorama.addScene(sceneId, this.streetViewList[sceneId]);
				this.prevSceneId.push(sceneId);
			}
		},
		async createPolyLine(type) {
			if(!this.polyLine.hasOwnProperty(this.listQuery.distIndex)) this.polyLine[this.listQuery.distIndex] = {};
			this.polyLine[this.listQuery.distIndex][type] = [];
			const lineInfo = this.panoramaInfo.list[this.listQuery.distIndex][type];
			if (!lineInfo) return;

			this.setPanoramaScene();

			// 掛載 polyline Layer(street view)
			if (this.polyLine[this.listQuery.distIndex][type].length > 0) {
				for (const polyLine of this.polyLine[this.listQuery.distIndex][type]) polyLine.setMap(this.map);
			} else {
				lineInfo.forEach((polyInfo, index) => {
					this.polyLine[this.listQuery.distIndex][type].push(
						new google.maps.Polyline({
							path: polyInfo.map((info) => info.position),
							geodesic: true,
							strokeColor: this.panoramaInfo.type[type].color,
							strokeOpacity: 1,
							strokeWeight: 3,
							map: this.map,
						})
					);

					this.polyLine[this.listQuery.distIndex][type][index].addListener("click", (event) => {
							const pointPos = event.latLng.toJSON();
							const posList = this.panoramaInfo.list[this.listQuery.distIndex][type][index].map(info => ({ ...info.position, sceneId: info.fileName }));
							const minDistObj = posList.reduce(
								(minDistObj, curr) => {
									const distance = Math.sqrt(Math.pow(pointPos.lat - curr.lat, 2) + Math.pow(pointPos.lng - curr.lng, 2));
									if (minDistObj.dist > distance) {
										minDistObj.dist = distance;
										minDistObj.sceneId = curr.sceneId;
									}
									return minDistObj;
								},
								{ dist: Infinity, sceneId: "" }
							);
							this.showPanoramaLayer(minDistObj.sceneId);
						}
					);
				});
			}
		},
		async createMarker(type) {
			// if(this.markers.panorama[this.listQuery.distIndex] && this.markers.panorama[this.listQuery.distIndex][type] ) {
			// 	for (const marker of Object.values(this.markers.panorama[this.listQuery.distIndex][type])) marker.setMap(null);
			// }
			if(!this.markers.panorama.hasOwnProperty(this.listQuery.distIndex)) this.markers.panorama[this.listQuery.distIndex] = {};
			this.markers.panorama[this.listQuery.distIndex][type] = [];
			const markerInfos = this.panoramaInfo.list[this.listQuery.distIndex][type];
			if (!markerInfos) return;

			this.setPanoramaScene();

			// 掛載 marker (street view)
			if (this.markers.panorama[this.listQuery.distIndex][type].length > 0) {
				for (const markers of this.markers.panorama[this.listQuery.distIndex][type]) markers.setMap(this.map);
			} else {
				const iconList = ["icon_blueDot.png", "icon_greenDot.png", "icon_redDot.png", "icon_yellowDot.png", "icon_greenDot.png"];
				const typeIndex = this.listQuery.streetViewType.indexOf(type);

				const icon = { 
					url: `${this.publicPath}assets/icon/${iconList[typeIndex]}`,
					scaledSize: new google.maps.Size(7, 7) 
				} 
				// const icon = { 
				// 	path: google.maps.SymbolPath.CIRCLE,
				// 	strokeWeight: 0,
				// 	fillColor: this.panoramaInfo.type[type].color,
				// 	fillOpacity: 1,
				// 	scale: 3
				// } 

				markerInfos.forEach((markerInfo, index) => {
					markerInfo.forEach((info, index) => {
						const marker = new google.maps.Marker({ 
							title: info.fileName,
							position: info.position, 
							icon: icon,
							map: this.map
						})
						this.markers.panorama[this.listQuery.distIndex][type].push(marker);

						marker.addListener("click", (event) => {
							const sceneId = marker.getTitle();
							console.log(sceneId);
							this.showPanoramaLayer(sceneId);
						});

						marker.addListener("mouseover", (event) => {
							this.infoWindow.setContent(event.latLng.toString());
							this.infoWindow.setOptions({ pixelOffset: new google.maps.Size(0, -10)});
							this.infoWindow.setPosition(event.latLng);

							this.infoWindow.open(this.map);
						});

						marker.addListener("mouseout", (event) => {
							this.infoWindow.close();
						});
					})
				});
			}
		},
		switchPanoramaLayer(type)  {
			const tempList = this.listQuery.streetViewType.filter(t => t != type);
			if(tempList.length == this.listQuery.streetViewType.length) this.listQuery.streetViewType.push(type);
			else this.listQuery.streetViewType = tempList;

			// for (const polyLineList of Object.values(this.polyLine)) for (const polyLine of Object.values(polyLineList)) polyLine.setMap(null);
			for(const polyline of Object.values(this.flattenObj(this.polyLine)).flat()) polyline.setMap(null);
			// for (const markers of Object.values(this.markers.panorama)) for(const marker of Object.values(markers)) marker.setMap(null);
			for(const markers of Object.values(this.flattenObj(this.markers.panorama)).flat()) markers.setMap(null);

			for(const type of this.listQuery.streetViewType) {
				if(!this.listQuery.streetViewPointMode && this.polyLine.hasOwnProperty(this.listQuery.distIndex))
					for (const polyLine of this.polyLine[this.listQuery.distIndex][type]) polyLine.setMap(this.map);
				
				if(this.listQuery.streetViewPointMode && this.markers.panorama.hasOwnProperty(this.listQuery.distIndex)) 
					for (const markers of this.markers.panorama[this.listQuery.distIndex][type]) markers.setMap(this.map);
			} 

		},
