			console.log(sceneId);

			// if (this.panoramaInfo.hotSpotsList.hasOwnProperty(this.listQuery.distIndex)) {
			// 	const hotSpotList = this.panoramaInfo.hotSpotsList[this.listQuery.distIndex];
			// 	if(hotSpotList[sceneId] != undefined) {
			// 		for(const hotSpot of hotSpotList[sceneId]) {
			// 			this.panorama.addHotSpot(hotSpot, sceneId);
			// 		}
			// 	}
			// }

			this.panorama.loadScene(sceneId);
			this.showPanorama = true;
		},
		showSpecPanorama(evt_info) {
			// console.log(this.markerPos);
			const type = (this.listQuery.layerType == "sidewalk") ? "pano_walk" : "pano_road";
			console.log(this.listQuery.layerType == "sidewalk", type);
			const posList = Object.values(this.panoramaInfo.list[this.listQuery.distIndex][type]).flat().map(info => ({ ...info.position, sceneId: info.fileName }));
			const minDistObj = posList.reduce(
				(minDistObj, curr) => {
					const distance = Math.sqrt(Math.pow(this.markerPos.lat - curr.lat, 2) + Math.pow(this.markerPos.lng - curr.lng, 2));
					// console.log(distance, 0.0001, distance < 0.0001);
					if (distance < 0.0001 && minDistObj.dist > distance) {
						minDistObj.dist = distance;
						minDistObj.sceneId = curr.sceneId;
					}
					return minDistObj;
				},
				{ dist: Infinity, sceneId: "" }
			);
			console.log(minDistObj);
			if(minDistObj.sceneId.length != 0) {
				this.showPanoramaLayer(minDistObj.sceneId);
				// this.infoWindow.close();
				// this.listQuery.layerType = "panorama";
			} else this.showSnackbar("查無街景");

			// this.infoPanoramaBtn.removeEventListener('click', this.showSpecPanorama);
			if(evt_info) evt_info.stopPropagation();
		},
		clearAllLayer() {
			//清除PCI
			for (const [_, layer] of Object.entries(this.kmlLayer.PCILayer)) layer.setMap(null);
			// for (const [_, layer] of Object.entries(this.kmlLayer.caseLayer)) layer.setMap(null);
			this.markerClusterer.clearMarkers();

			//清除KML
			for (const [name, layer] of Object.entries(this.kmlLayer)) if(name != "PCILayer" && name != "caseLayer") layer.setMap(null);

			//清除data 圖層
			for(const dataLayer of Object.values(this.dataLayer.facility)) dataLayer.forEach(feature => dataLayer.remove(feature));
			for(const dataLayer of Object.values(this.dataLayer.sidewalk)) dataLayer.forEach(feature => dataLayer.remove(feature));
			// if(this.dataLayer.sidewalk != undefined) this.dataLayer.sidewalk.forEach(feature => this.dataLayer.sidewalk.remove(feature));
			// this.map.data.forEach(feature => this.map.data.remove(feature));

			//清除街景路線
			// for (const polyLineList of Object.values(this.polyLine)) for (const polyLine of Object.values(polyLineList)) polyLine.setMap(null);
			for(const polyline of Object.values(this.flattenObj(this.polyLine)).flat()) polyline.setMap(null);
			// for (const markers of Object.values(this.markers.panorama)) for(const marker of Object.values(markers)) marker.setMap(null);
			for(const markers of Object.values(this.flattenObj(this.markers.panorama)).flat()) markers.setMap(null);

			//關閉info window
			this.infoWindow.close();
		},
		delPCIInfo(item) {
			const index = this.PCIInfo.list.indexOf(item);
			this.PCIInfo.list.splice(index, 1);
		},
		showSnackbar(text, timeout = 3000) {
			this.snackbarInfo.text = text;
			this.snackbarInfo.timeout = timeout;
			this.snackbarInfo.show = true;
		},
		flattenObj(obj) {
			let result = {};
			for(const i in obj) {
				if((typeof obj[i]) === 'object' && !Array.isArray(obj[i])) {
					const temp = this.flattenObj(obj[i]);
					for(const j in temp) result[`${i}.${j}`] = temp[j];
				} else result[i] = obj[i];
			}
			return result;
		}
	},
};
</script>

